import React from "react";
import { BrowserRouter, Switch } from "react-router-dom";

import Home from "./screens/Home";
import Contact from "./screens/Contact";
import ForCompanies from "./screens/ForCompanies";
import About from "./screens/About";
import Position from "./screens/Position";
import Search from "./screens/Search";
import PrivacyPolicy from "./screens/PrivacyPolicy";
import NavigationMonitor from "./components/NavigationMonitor";
import CustomRoute from "./components/CustomRoute";
import ShareModal from "./components/ShareModal";
import SnackBar from "./components/SnackBar";
import { ModalProvider } from "./contexts/Modal";
import { SnackBarProvider } from "./contexts/SnackBar";

function App() {
  return (
    <SnackBarProvider>
      <ModalProvider>
        <BrowserRouter>
          <SnackBar />
          <ShareModal />
          <NavigationMonitor />
          <Switch>
            <CustomRoute exact path="/" component={Home} />
            <CustomRoute path="/quem-somos" component={About} />
            <CustomRoute
              path="/para-empresas"
              component={ForCompanies}
              options={{
                headerVersion: "institutional",
                footerVersion: "institutional",
              }}
            />
            <CustomRoute path="/contato" component={Contact} />
            <CustomRoute path="/vaga/:slug/:id" component={Position} />
            <CustomRoute exact path="/busca" component={Search} />
            <CustomRoute
              exact
              path="/politica-de-privacidade"
              component={PrivacyPolicy}
              options={{
                headerVersion: "simple",
              }}
            />
          </Switch>
        </BrowserRouter>
      </ModalProvider>
    </SnackBarProvider>
  );
}

export default App;
