import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";

import { Blocks, Mail } from "../../libs/icons";
import IconLink from "../../components/IconLink";
import SearchField from "../../components/SearchField";

import Logo from "./logo-inklua-color.svg";
import styles from "./styles.module.scss";

const Header = () => {
  const [isFloating, setFloating] = useState(false);

  const handleScroll = () => {
    setFloating(window.scrollY > 0);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <>
      <span className={styles.spacer} aria-hidden="true" />
      <header
        className={[styles.wrapper, isFloating && styles.floating].join(" ")}
      >
        <div className={styles.container}>
          <Link to="/" aria-label="Início">
            <img src={Logo} alt="Inklua" />
          </Link>
          <SearchField />
          <div className={styles.links}>
            <IconLink text="Home" to="/" type="link">
              <Blocks color="#1B9B9E" />
            </IconLink>
            <IconLink text="Contato" to="/contato" type="link">
              <Mail color="#1B9B9E" />
            </IconLink>
          </div>
        </div>
      </header>
    </>
  );
};

Header.propTypes = {};

export default Header;
